import BacketItems from "../backetItems/backetItems";

import { ShoppingBasket } from "@mui/icons-material";
import { Divider, Drawer, List, ListItem, ListItemIcon, ListItemText, Typography } from "@mui/material";


const Backed = (props) => {
    const {
        //    
        cardOpen, //false
        cardClose = Function.prototype, 
        //
        order = [], 
        //  
        removeFromOrder
    } = props;

    return(
        <Drawer
            anchor="right"
            //          
            open={cardOpen}
            //          Drawer
            onClose ={cardClose}>

                <List sx={{width: 300}}>
                    <ListItem>
                        <ListItemIcon>
                            <ShoppingBasket/>
                        </ListItemIcon>
                        <ListItemText primary="" />
                    </ListItem>

                    <Divider/>

                    {
                        !order.length ? 
                        (
                        //   
                        <ListItem>  </ListItem>
                         //     
                        )  : (
                                <> {
                                        order.map((item) => {
                                            return(<BacketItems 
                                                key={item.name} 
                                                removeFromOrder={removeFromOrder}
                                                //  props
                                                {...item}/>
                                                );
                                            
                                        })
                                    }
                                </>
                            )
                    }     
                </List>
        </Drawer>
    )

}
export default Backed;