
import { useState } from 'react';

import { Container } from '@mui/material';
import { WithErrorBoundary } from 'react-error-boundary';



import Header from '../header/Header.js';
import Search from '../search/Search.js';
import ListCard from '../listard/ListCard.js';
import Backed from '../backet/Backed.js';
import Snack from '../snack/Snack.js';
import ErrorFallback from '../errorFallback/ErrorFallback.js';

import json from '../../data/data.json';


const App = () => {
    let goods = json;
    //  
    const [order, setOrder] = useState([]);
    console.log(`setOrder ${setOrder}, order ${order} `);
    //  
    const [search, setSearch] = useState('');
    //  
    const [products, setProducts] = useState(goods);
    //     
    const [isCardOpen, SetCardOpen] = useState(false);
    //        
    const [isShackOpen, setSnackOpen] = useState(false);

    const [ isLoading, setLoading] = useState(true);
    const [ error, setError] = useState(false);


    //   ,   .then  .catch
    //    setLoading(false)
    fetch('../../data/data.json')
    .then(response => response)
    .then(json1 => {
        console.log(json1);
        setLoading(false);
    })


    const GetPost = () => {
        setLoading(false)

    };

    //    
    const handleChange = (e) => {
        //      
        if (!e.target.value) {
            setProducts(goods);
            setSearch('');
            return;
        }
        //     ,     
        setSearch(e.target.value);
        setProducts(
            products.filter((good) =>
                good.blend_name.toLowerCase().includes(e.target.value.toLowerCase())
            ))
    };
    //    
    const addToOrder = (goodsItem) => {
        let quantity = 1;

        const indexInOrder = order.findIndex(
            (item) => item.id === goodsItem.id
        );

        if (indexInOrder > -1) {
            quantity = order[indexInOrder].quantity + 1;

            setOrder(order.map((item) => {
                    if (item.id !== goodsItem.id) return item;

                    return {
                        id: item.id,
                        name: item.name,
                        quantity,
                    };
                }),
            );
        } else {
            setOrder([
                    ...order,
                    {
                        id: goodsItem.id,
                        name: goodsItem.name,
                        quantity,
                    },
                ],
            );
        }

        //       
        setSnackOpen(true);    
    };
    //     
    const removeFromOrder = (goodsItem) => {
        setOrder(order.filter((item) => item.id !== goodsItem));
    };
    
    return (
        <div>
            
            <Header
            //     true
                handleCard={()=> {SetCardOpen(true)}}
                orderLength={order.length}
                    >
            </Header>
            <Container 
                sx={{mt:2}}>
                <Search
                    // 
                    value={search}
                    //    onChange     
                    onChange={handleChange}>
                </Search>   
                <ListCard 
                    //         
                    goods={products} 
                    setOrder={addToOrder}
                    isLoading={isLoading}>
                </ListCard>
                
            </Container>
            <Backed 
                //    
                cardOpen={isCardOpen} 
                //       false
                cardClose={()=> SetCardOpen(false)}
                removeFromOrder={removeFromOrder}
                order={order}>
            </Backed>
            <Snack 
                // 
                isCardOpen={isShackOpen}
                //    
                handleClose={() => setSnackOpen(false)}
                />
        </div>
    )
}
export default App;